/**
  GENERAL */
html {
  display: block;
  height: 100%;
}
html, body {
  width: 100%;
}

body {
  font-family: 'latoregular', sans-serif;
  font-size: 18px;
  line-height: 1.5;
  font-weight: normal;
  color: $white;
  background: $dark;
  min-height: 100%;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility !important;
  display: flex;
  flex-direction: column;
}

h1, h2, h3, h4 {
  font-family: 'latoblack', sans-serif;
  font-weight: normal;
}

.highlight-white {
  font-weight: normal;
  color: $white;
}


.overlay-active {
  

  @include media-query($mobile) {
    overflow: hidden;
  }
}

a {
  cursor: pointer;
}

/**
  HOME */

.home, .thanks {
  background: $dark url('../assets/backgrounds/bg-2019-placeholder.png') no-repeat;
  background-size: 100% auto;
  background-position: top center;
  background-image: -webkit-image-set(url('../assets/backgrounds/bg-2019-placeholder.png')1x,url('../assets/backgrounds/bg-2019-placeholder@2x.png')2x);

  @include media-query($mobile) {
    background-image: none;
  }

  @include media-query($medium) {
    .speaker-item:last-of-type {
      display: none;
    }
  }
  @include media-query($small) {
    .speaker-item:nth-child(5) {
      display: none;
    }
    .speaker-item:last-of-type {
      display: none;
    }
  }
}

.thanks {
  background: $dark url('../assets/backgrounds/bg-2019-placeholder.png') no-repeat;
  background-size: 100% auto;
  background-position: top center;
  background-image: -webkit-image-set(url('../assets/backgrounds/bg-2019-placeholder.png')1x,url('../assets/backgrounds/bg-2019-placeholder@2x.png')2x);
}


/**
  NOTICE */

.notice-bar {
  display: block;
  width: 100%;
  border-top: 1px solid rgba($white, .5);
  border-bottom: 1px solid rgba($white, .5);
  box-sizing: border-box;
  padding: 12px 0;
  margin-bottom: 40px;

  color: rgba($white, .85);

  @include media-query($mobile) {
    padding: 12px 5%;
  }

  h3 {
    color: $yellow;
  }

}


/**
  TRANSITION VIEW - ARCHIVE */

.archive-transition {
  display: block;
  width: 100vw;
  height: 100vh;
  background: $yellow;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}

.archive-page {
  animation: fadeIn 2s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  top  { opacify: 1; }
}

/**
  HEADER */

.header {
  display: block;
  width: 100%;
  max-width: 1440px;
  height: auto;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  position: relative;

  @include media-query-min($giant) {
    max-width: 1680px;
  }
  @include media-query($medium) {
    max-width: 1200px;
  }

  @include media-query($small) {
    max-width: 100%;
    padding: 0 40px;
  }
  @include media-query($mobile) {
    height: auto;
    padding: 0 12px;
    margin-bottom: 24px;

    &-active {
      background: $blue-darken;

      .menu--third {
        display: none;
      }
    }
  }

  &--row {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding-top: 65px;

      @include media-query($mobile) {
        padding-top: 24px;
        justify-content: space-between;
      }
  }
}

.menu-item {
  font-family: 'latoblack', sans-serif;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 2px;
  color: $white;
  text-transform: uppercase;
  text-decoration: none;
  padding: 12px;
  box-sizing: border-box;

  display: none;

  @include media-query($mobile) {
    display: block;
    margin-right: -12px;
  }

  &-linethrough {
    position: relative;
  }

  .menu-icon {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 12px;
    margin-left: 8px;

    &:before {
      display: block;
      content: '';
      width: 30px;
      height: 2px;
      border-radius: 1px;
      background: $white;
      position: absolute;
      top: 2px;
    }
    &:after {
      display: block;
      content: '';
      width: 30px;
      height: 2px;
      border-radius: 1px;
      background: $white;
      position: absolute;
      top: 10px;
    }
  }

  &-active {

    .menu-icon {
      &:before {
        background: $white;
        transform: rotate(45deg);
        top: 6px;
        width: 25px;
      }
      &:after {
        background: $white;
        transform: rotate(-45deg);
        top: 6px;
        width: 25px;
      }
    }
  }
}

.menu--third {
  display: none;

  @include media-query($mobile) {
    display: flex;
  }

  a {
    font-family: 'latoblack', sans-serif;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 2px;
    color: $white;
    text-transform: uppercase;
    text-decoration: none;
    margin-left: auto;
    padding: 0 12px;
    box-sizing: border-box;
  }
}

.menu--primary {
  display: block;
  margin-left: auto;

  @include media-query($mobile) {
    display: none;
  }

  &.menu-open {
    background: $blue-darken;
    box-sizing: border-box;
    padding: 0;
    width: 100%;
    display: block;

    position: absolute;
    top: 74px;
    left: 0;
    z-index: 100;

    ul {
      display: block;
    }

    .menu--secondary {
      display: block;
      position: relative;

      ul li {
        width: auto;
        display: inline-block;
      }
    }
  }

  ul {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    list-style: none;

    @include media-query($mobile) {
      margin-top: 10px;
      flex-wrap: wrap;
      -webkit-flex-wrap: wrap;
      display: none;
    }

    li {
      margin-left: 24px;

      &.search-block {
        position: relative;

        &:before {
          display: block;
          content: '';
          width: 1px;
          height: 12px;
          background: rgba($white, .5);
          position: absolute;
          top: 37%;
        }

        svg {
          margin-left: 3px;
        }
      }

      &.search-block-item {
        display: none;

        @include media-query($mobile) {
          display: block;
        }

        svg {
          margin-left: 5px;
        }
      }

      @include media-query-min($large) {
        margin-left: 30px;
      }

      @include media-query($small) {
        margin-left: 12px;
      }

      @include media-query($mobile) {
        margin-left: 0;
        display: block;
        width: 100%;
      }

      a {
        font-family: 'latoblack', sans-serif;
        font-weight: normal;
        font-size: 11px;
        letter-spacing: 2px;
        color: $white;
        text-decoration: none;
        text-transform: uppercase;
        text-shadow: 0 1px 3px $dark;

        @include transition-ease;

        @include media-query-min($giant) {
          font-size: 12px;
        }

        &:hover {
          color: $yellow;
        }

        &.menu-item-linethrough {

          span {
            text-decoration: line-through;
          }

          .menu-item-tickets-addon {
            text-decoration: none;
            margin-left: 3px;
            color: $blue;
          }
        }

        @include media-query($mobile) {
          display: block;
          color: $light-gray;
          padding: 10px 0 10px 20px;
          box-sizing: border-box;
          font-size: 14px;
          text-shadow: none;
        }
      }
    }

    .submenu-prim {
      display: block;
      position: relative;

      a {
        position: relative;
        z-index: 30;
      }

      &:hover {

        .submenu {
          display: block;
        }
      }

      .icon-arrow-down {
        display: inline-block;
        margin-left: 10px;


        > svg {
          width: auto;
          height: 8px;
        }
      }

      .submenu {
        display: none;
        width: auto;
        position: absolute;
        top: 40px;
        right: 0;
        background: $blue-dark;
        box-sizing: border-box;
        border-radius: 2px;
        z-index: 20;
        margin: 0;
        white-space: nowrap;

        @include media-query($mobile) {
          display: none;
          position: relative;
          top: 0;
          background: none;
          width: 100%;
        }

        &:after {
          display: block;
          content: '';
          height: 210px;
          width: calc(100% + 20px);
          background: rgba($white, .0);

          position: absolute;
          top: -30px;
          left: -10px;
          z-index: -1;

          @include media-query($mobile) {
            display: none;
          }
        }

        li {
          display: block;
          box-sizing: border-box;
          border-bottom: 1px solid $dark;
          margin-left: 0;

          @include media-query($mobile) { 
            border-bottom: none;
          }

          &:last-of-type {
            border-bottom: none;
          }

          a {
            display: block;
            padding: 14px 24px;
            box-sizing: border-box;

            @include media-query($mobile) {
              font-size: 12px;
              position: relative;
              padding: 8px 20px 8px 40px;

              &:before {
                display: block;
                content: '';
                width: 8px;
                height: 1px;
                background: $blue-dark;
                position: absolute;
                left: 20px;
                top: 50%;
              }
            }
          }
        }
      }
    }
  }
}

.menu--secondary {
  display: block;
  background: $yellow;
  width: auto;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0 0 4px 4px;
  padding: 0 10px;
  box-sizing: border-box;

  @include media-query($small) {
    right: 20px;
  }
  @include media-query($mobile) {
    display: none;
    right: 0;
  }

  ul {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: flex-start;
    -webkit-align-items: flex-start;
 
    flex-direction: row;
    -webkit-flex-direction: row;

    li {
      margin-left: 0;

      a {
        display: block;
        font-family: 'latobold', sans-serif;
        font-weight: normal;
        font-size: 10px;
        letter-spacing: 2px;
        color: $dark;
        text-decoration: none;
        text-transform: uppercase;
        padding: 14px 10px;
        box-sizing: border-box;
        text-shadow: none;

        @include transition-ease;

        @include media-query-min($giant) {
          font-size: 12px;
        }

        @include media-query($mobile) {
          font-size: 12px;
          padding: 14px 8px;
        }

        &:hover {
          color: $white;

          #facebook, #twitter, #instagram, #search {
            fill: $white;
          }
        }

        #facebook, #twitter, #instagram, #search {
          fill: $dark;
          box-shadow: none;
          @include transition-ease;
        }
      }
    }
  }
}

/**
  SEARCH FIELD */

.search--nav {
  display: none;
  position: absolute;
  top: 0;
  right: -10px;

  @include media-query($mobile) {
    display: block;
  }

  &.search-open {
    display: block;
    z-index: 100;
    width: 376px;
  }

  input[type="search"] {
    width: 274px;
    background: $yellow;
    padding: 10px 10px 9px;
    margin: 0;
    border: none;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    vertical-align: top;
    border-radius: 0 0 0 2px;
    box-sizing: border-box;

    font-family: 'latobold', sans-serif;
    font-size: 16px;
    color: $white;

    &::placeholder {
      color: rgba($white, .8);
    }

    &:focus {
      outline: none;
    }
  }

  .search-btn-close {
    display: inline-block;
    background: $yellow;
    font-family: 'latoblack', sans-serif;
    font-size: 9px;
    font-weight: normal;
    letter-spacing: 2px;
    color: $white;
    padding: 14px 20px;
    box-sizing: border-box;
    text-transform: uppercase;
    text-shadow: 0 1px 3px rgba(174, 91, 0, .8);

    &:hover {
      cursor: pointer;
      color: $dark;
    }

    .close-icon {
        display: inline-block;
        position: relative;
        width: 11px;
        height: 9px;
        margin-left: 5px;

        &:before {
          display: block;
          content: '';
          width: 9px;
          height: 1px;
          border-radius: 1px;
          background: $white;
          position: absolute;
          top: 5px;
          transform: rotate(45deg);
        }
        &:after {
          display: block;
          content: '';
          width: 9px;
          height: 1px;
          border-radius: 1px;
          background: $white;
          position: absolute;
          top: 5px;
          transform: rotate(-45deg);
        }
      }
  }
}

#results-container.search--nav-list {
  flex-direction: column;
  -webkit-flex-direction: column;

  background: $blue-dark;
  width: 356px;
  margin-left: 10px;
  border-radius: 0 0 2px 2px;
  box-sizing: border-box;

  li {
    margin-left: 0;
    padding: 0;
    box-sizing: border-box;
    display: block;
    width: 100%;

    &:last-of-type a {
      border-radius: 0 0 2px 2px;
    }

    a {
      font-family: 'latoregular', sans-serif;
      font-size: 14px;
      padding: 8px 20px;
      box-sizing: border-box;
      letter-spacing: 0;
      text-transform: none;
      text-shadow: none;

      &:hover {
        background: $white;
      }
    }
  }
}

.search--bar {

  @include media-query($mobile) {
    padding: 0 20px;
    box-sizing: border-box;
  }

  input[type="search"] {
    width: 100%;
    background: none;
    padding: 20px 0;
    margin: 0;
    border: none;
    border-bottom: 1px solid $blue-dark;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    vertical-align: top;
    border-radius: 0 0 0 2px;
    box-sizing: border-box;

    font-family: 'latobold', sans-serif;
    font-size: 36px;
    color: $white;

    @include media-query($mobile) {
      font-size: 16px;
    }

    &::placeholder {
      color: rgba($white, .8);
    }

    &:focus {
      outline: none;
    }
  }


  &-list {
    display: block;
    margin-top: 20px;
    list-style: none;

    li {
      display: block;
      padding: 8px 0;
      box-sizing: border-box;
      border-bottom: 1px solid $blue-dark;

      span {
        font-family: 'latobold', sans-serif;
        font-size: 10px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: rgba($white, .35);
      }

      a {
        display: block;
        font-family: 'latoregular', sans-serif;
        font-size: 21px;
        font-weight: normal;
        color: $white;
        text-decoration: none;

        @include media-query($mobile) {
          font-size: 16px;
        }

        &:hover {
          color: $yellow;
        }
      }
    }
  }
}

/**
  BREADCRUMBS */

.pagination-nav {
  display: flex;
  padding: 0;
  margin-bottom: 76px;
  border: none;
  list-style: none;

  @include media-query($mobile) {
    padding: 0;
    box-sizing: border-box;
    margin-bottom: 24px;
    flex-wrap: wrap;

    position: relative;
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    width: 200%;
    white-space: nowrap;
  }

  li {
    display: block;
    font-family: 'latoblack', sans-serif;
    font-size: 12px;
    font-weight: normal;
    line-height: 2.681;
    letter-spacing: 2px;
    color: $gray;
    text-transform: uppercase;

    &:after {
      display: inline-block;
      content: '|';
      color: $gray;
      margin-left: 14px;
      transform: rotate(25deg);

      @include media-query($mobile) {
        display: none;
      }
    }
    &:before {
      display: inline-block;
      content: '|';
      color: $gray;
      margin-left: 0;
      margin-right: 14px;
      transform: rotate(25deg);

      @include media-query($mobile) {
        margin-right: 7px;
        margin-left: 7px;
      }
    }

    @include media-query($mobile) {
      display: inline-block;
      font-size: 10px;

      &:first-of-type, &:first-of-type:after, &:nth-child(2):before {
        display: none;
      }
    }

    &:last-of-type:after, &:first-of-type:before {
      display: none;
    }
    &:last-of-type, &:last-of-type a {
      color: $white;
    }

    a {
      color: $gray;
      text-decoration: none;

      @include transition-ease;

      &:hover {
        color: $yellow;
      }
    }
  }
}

/* ARCHIVE - COLLECTED */

.topbar {
  display: flex;
  width: 100%;
  background: $blue-darken;
  padding: 12px 0;
  box-sizing: border-box;

  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;

    @include media-query-min($large) {
      max-width: 1680px;
    }
  }

  &-link {
    font-family: 'latoregular', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $light-gray;
    text-decoration: none;

    @include transition-ease;

    &:hover {
      color: $yellow;
    }
  }

  &-meta {
    font-family: 'latoblack', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $yellow;
  }
}

.archive-block {

  &-item {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-bottom: 24px;
    margin-right: 24px;

    text-decoration: none;


    &.col-2-8:nth-child(even) {
      margin-right: 0;
    }


    .heading {
      font-family: 'latobold', sans-serif;
    }
    .sub-headline {
      color: $gray;
    }
  }
}

.archive-row {

  &-item {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $blue-darken;
    padding: 12px 0;
    box-sizing: border-box;

    h2 {

      span {
        display: block;
      }
    }
  }
}

.presentation-block {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.presentation-block-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 24px;
  margin-bottom: 48px;

  &:nth-child(3n+3) {
    margin-right: 0;
  }

  .archive-block-item:first-of-type {
    margin-top: 20px;
  }
}

.presentation-row {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;


  &-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }

  &.videos-row {

    .archive-row-item {
    
      &:nth-child(n+10) {
        display: flex;
      }

      h3 {
        width: 25%;
      }
    }
  }

  .archive-row-item {
    position: relative;
    text-decoration: none;

    @include transition-ease;

    &:hover {
      background: $blue-darken;

      h3 {
        padding-left: 24px;
      }
    }

    h3 {
      order: 1;
      width: 15%;
      box-sizing: border-box;

      @include transition-ease;
    }
    h4 {
      order: 2;
      width: 10%;
    }
    h2 {
      order: 3;
      width: 75%;

      &:hover {
        color: $yellow;
        cursor: pointer;
      }
    }

    &-double {
      display: flex;
      order: 2;
      width: 85%;
      flex-wrap: wrap;

      h2 {
        width: 88%
      }
      h4 {
        width: 10%;
      }

      span {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-items: center;
        width: 100%;
      }
    }

    &:nth-child(n+10) {
      display: none;
    }
  }

  &-extended {
    
    .archive-row-item {

      &:nth-child(n+10) {
        display: flex;
      }
    } 
  }

  &-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 0;
    box-sizing: border-box;
    background: rgba($blue-darken, .25);

    font-family: 'latoblack', sans-serif;
    font-size: 11px;
    line-height: 32px;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $light-gray;

    @include transition-ease;

    &:after {
      display: block;
      content: 'Show more +';
    }

    &:hover {
      color: $yellow;
      cursor: pointer;
    }


    &-active {
      color: $white;
      
      &:after {
        content: 'Show less -';
      }
    }
  }
}

.keynote-block .archive-block-item {
  margin-bottom: 60px;
}



.archive-list {

  li {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-top: 1px solid rgba($white, .25);

     span {
      display: block;
      width: 25%;
     }
  }
}


.archive-header-title {
  font-family: 'latoblack', sans-serif;
  font-size: 96px;

  &-divider {
    display: block;
    content: '';
    width: 2px;
    height: 20px;
    border-radius: 2px;
    background: $blue-darken;
    margin-left: 110px;
  }

  &-gray {
    color: $blue-darken;
  }

  &-yellow {
    color: $yellow;
  }
}


.button-group-block {

}

.training-row-item {
  justify-content: flex-start;

  h3 {
    order: 1;
    width: 15%;
  }
  h4 {
    order: 2;
    width: 10%;
  }
  h2 {
    order: 3;
    width: 75%;
  }
}



// ARCHIVE KEYNOTE BLOCK

.archive-keynote {

  &-block {


    &-item {
      display: flex;
