  }
  &-start {
    align-items: flex-start;
  }
  &-bit {
    margin-bottom: 38px;

    @include media-query($mobile) {
      margin-bottom: 24px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
    &:first-of-type {
      margin-bottom: 38px;
    }
  }
  &-align-right {
    text-align: right;
  }

  &-image {
    display: block;
    width: 100%;
    overflow: hidden;
    position: relative;

    &.image-square {
      height: 708px;

      @include media-query($mobile) {
        height: auto;
      }
    }
    &.image-tall {
      height: auto;

      &:before, &:after {
        width: 176px;
      }
      .fade-block {
        height: 176px;
      }
    }

    &:before {
      display: block;
      content: '';
      width: 76px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 110;
      background: rgb(3,1,0);
      background: linear-gradient(90deg, rgba(3,1,0,1) 0%, rgba(3,1,0,0) 100%);
    }
    &:after {
      display: block;
      content: '';
      width: 76px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 110;
      background: rgb(3,1,0);
      background: linear-gradient(270deg, rgba(3,1,0,1) 0%, rgba(3,1,0,0) 100%);
    }

    img {
      display: block;
      width: 100%;
      z-index: 100;
    }

    .fade-block {
      display: block;
      content: '';
      width: 100%;
      height: 76px;
      position: absolute;
      left: 0;
      z-index: 110;

      &.bottom-fade {
        bottom: 0;
        background: -webkit-linear-gradient(transparent, rgba($dark, 1)) top;
        background: linear-gradient(transparent, rgba($dark, 1)) top;
      }
      &.top-fade {
        top: 0;
        background: -webkit-linear-gradient(rgba($dark, 1), transparent) bottom;
        background: linear-gradient(rgba($dark, 1), transparent) bottom;
      }
    }
  }

  &.program-training {

    @include media-query($mobile) {
      flex-direction: column-reverse;

      .col-3-8 {
        margin-bottom: 24px;
      }
    }
  }

  &.practical-details {

    @include media-query($mobile) {

      .spacing-bottom {
        margin-bottom: 0;
      }
    }
  }

  &.expo-group {
    @include media-query($mobile) {

