/**
  CARDS */

.card-item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  border-radius: 2px;
  padding: 38px;
  box-sizing: border-box;

  text-decoration: none;

  @include media-query($mobile) {
    padding: 24px;
    margin-bottom: 24px;
  }

  &-filled {
    background: $blue-darken;
  }
  &-row {
    flex-direction: row;
    justify-content: space-between;
  }

  .card-details-block {
    margin-bottom: 12px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &-center {
    justify-content: center;

    .card-item-details {
      margin-top: 0;
    }
  }

  &-linked {

    @include transition-ease;

    &:hover {
      background: rgba($blue-darken, .5);

      .subtitle-yellow {
        color: $white;
      }
    }
  }
  &-training {

    @include media-query($mobile) {
      &.col-2-8 {
        width: 100%;
      }
    }
  }

  &-header {
    display: flex;
    justify-content: space-between;
  }

  &-image {
    display: block;
    width: calc(100% + 76px);
    margin-bottom: 24px;
    margin-left: -38px;
    margin-top: -38px;
    border-radius: 2px 2px 0 0;
    overflow: hidden;

    @include media-query($mobile) {
      width: calc(100% + 48px);
      margin-left: -24px;
      margin-top: -24px;
    }

    img {
      display: block;
      width: 100%;
    }
  }

  &-image-detailed {
    display: block;
    width: 100%;

    img {
      display: block;
      width: 100%;
      border-radius: 2px 2px 0 0;
    }

    &-meta {
      display: block;
      width: 100%;
      border-radius: 0 0 2px 2px;
      box-sizing: border-box;
      padding: 14px 38px;
      background: $blue-darken;

      &.sub-headline {
        font-size: 12px;
        color: $gray;
      }
    }
  }

  .button {
    margin-top: 38px;

    &.button-rectangular {
      border-radius: 2px;
    }
    &.button-small-padding {
      padding: 10px 12px;
    }
  }
  &-details {
    margin-top: 12px;

    .speaker-name span:nth-child(2):before {
      display: inline-block;
      content: '&';
      width: auto;
      height: auto;
      margin: 0 6px 0 4px;
    }
  }

  &-details-row {
    display: flex;
    justify-content: space-between;
  }
}


