    letter-spacing: 1px;
    text-transform: uppercase;
  }

  .workshop--list-item {
    display: block;
    padding: 10px 0 0;
    margin: 10px 0 0;
    box-sizing: border-box;
    border-top: 1px solid rgba($white, .25);

    &.workshop--list-item-extended .workshop--meta {
      min-width: 210px;

      @include media-query-min($large) {
        min-width: 240px;
      }
    }
  }

  .workshop--list-link {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: center;
    -webkit-align-items: center;

    color: $white;
    text-decoration: none;

    @include transition-ease;

    @include media-query($mobile) {
      flex-wrap: wrap;
      -webkit-flex-wrap: wrap;
    }

    &:hover {
      color: $yellow;

      .workshop--meta-category.category-news, .workshop--meta-category.category-press, .workshop--meta-author, .workshop--meta-time {
        color: $yellow;
      }
    }

    .workshop--meta {
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
    
      align-items: center;
      -webkit-align-items: center;
    
      margin-right: 20px;
      min-width: 175px;
      height: 27px;
    }
    .workshop--meta-date {
      min-width: 105px;
      color: $gray;
    }
    .workshop--meta-time {
      min-width: 50px;
    }

    .workshop--meta-time, .workshop--meta-author {
      color: rgba($white, .8);

      @include transition-ease;
    }

    .workshop--meta-date, .workshop--meta-category, .workshop--meta-author, .workshop--meta-time {
      display: inline-block;
      font-family: 'latoblack', sans-serif;
      font-weight: normal;
      font-size: 12px;
      text-transform: uppercase;
      letter-spacing: 2px;

      @include media-query-min($large) {
        font-size: 14px;
      }
    }
    .workshop--meta-category, .workshop--meta-author {
      margin-left: 15px;

      &.category-news {
        color: $blue;

        @include transition-ease;
      }
      &.category-press {
        color: $red;

        @include transition-ease;
      }
      &.category-blog {
        color: $yellow;

        @include transition-ease;
      }
    }

    .workshop--list-title {
      font-size: 18px;
      font-family: 'latobold', sans-serif;
      font-weight: normal;
    }
  }
}

/**
  WORKSHOP - BLOCK GROUP */

.workshop--block-group {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  align-items: flex-start;
  -webkit-align-items: flex-start;

  justify-content: space-between;
  -webkit-justify-content: space-between;

  flex-direction: row;
  -webkit-flex-direction: row;

  flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  width: 100%;

  @include media-query($mobile) {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .subpar-headline {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    font-family: 'latoregular', sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  .workshop--block-item {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: flex-start;
    -webkit-align-items: flex-start;
  
    flex-direction: row;
    -webkit-flex-direction: row;

    background: $blue-dark;
    box-sizing: border-box;
    width: calc(50% - 10px);
    border-radius: 2px;
    text-decoration: none;
    margin-bottom: 20px;

    @include transition-ease;

    &:hover {
      background: rgba($blue-dark, .8);
    }

    @include media-query-min($large) {
      width: calc(50% - 11px);
    }

    @include media-query($mobile) {
      margin-bottom: 20px;
      width: 100%;
    }

    img {
      display: block;
      width: 170px;
      max-width: 170px;
      border-radius: 2px 0 0 2px;
      box-sizing: border-box;

      @include media-query-min($large) {
        width: 205px;
        max-width: 205px;
      }

      @include media-query($mobile) {
        display: none;
      }
    }

    .workshop--block-content {
      display: block;
      width: calc(100% - 84px);
      padding: 20px;
      box-sizing: border-box;

      @include media-query-min($large) {
        padding: 20px 22px;
      }
      @include media-query($mobile) {
        width: 100%;
      }

      p {
        color: rgba($white, .8);
        margin-bottom: 0;
      }
    }

    .workshop--block-title {
      display: block;
      width: 100%;
      font-family: 'latoblack', sans-serif;
      font-size: 18px;
      font-weight: normal;
      color: $white;
    }

    .workshop--block-status {
      display: inline-block;
      padding: 6px 12px;
      box-sizing: border-box;
      border-radius: 4px;
      background: rgba($dark, .5);
      color: $white;
      margin-bottom: 12px;
      font-size: 12px;
      text-transform: uppercase;

      @include media-query($mobile) {
        font-size: 10px;
      }

      &-active {
        background: rgba($yellow, .9);
        color: $dark;
      }
    }

    .workshop--meta {
      display: block;
      margin-top: 5px;
      font-family: 'latobold', sans-serif;
      font-size: 12px;
      font-weight: normal;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: rgba($white, .5);
      padding-bottom: 10px;
      margin-bottom: 9px;
      box-sizing: border-box;
      border-bottom: 1px solid rgba($white, .1);

      &-time {
        margin-right: 10px;
      }
      &-author:nth-child(3) {

        &:before {
          display: inline-block;
          content: "\0026";
          margin-right: 4px;
        }
      }
    }
  }
}

/**
  SPEAKER LIST */

.speaker--block {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  &-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 38px;

    @include media-query($mobile) {
      margin-bottom: 24px;
      flex-wrap: wrap;
    }
  }

}

.speaker--list {


  &-group, &-group-special {

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;
    position: relative;

    @include media-query($mobile) {
      width: calc(100% + 24px);
      margin-left: -12px;
    }
  }

  &-group-special {

    .speaker--list-item {
      margin-bottom: 0;

      @include media-query($small) {
        width: 33.33333%;
        max-width: 33.33333%;
      }
      @include media-query($mobile) {
        width: 33.33333%;
        max-width: 33.33333%;
      }
      .speaker-block-data {

        @include media-query($small) {
          padding: 20px;

          h2 {
            font-size: 18px;
          }
          h4 {
            font-size: 11px;
          }
        }
        @include media-query($mobile) {
          padding: 10px;

          h2 {
            font-size: 3.5vw;
          }
          h4 span {
            display: none;
          }
        }
      }
    }

    .item-block {
      display: block;
      height: 336px;
      min-height: 336px;
      overflow: hidden;
      position: relative;
      text-decoration: none;
      color: $white;


      @include media-query-min($large) {
        height: 360px;
        min-height: 360px;
      }
      @include media-query($mobile) {
        height: 205px;
        min-height: 205px;
      }

      &:before {
        display: block;
        content: '';
        width: 100%;
        height: 50%;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 20;
        background: -webkit-linear-gradient(transparent, rgba($dark, .8)) top;
        background: linear-gradient(transparent, rgba($dark, .8)) top;

        @include transition-ease;

        @include media-query($mobile) {
          height: 40%;
        }
      }

      &:hover {
        color: $yellow;

        &:before {
          height: 100%;
        }
      }

      img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
  }

  &-item {
    display: block;
    width: 224px;
    max-width: 224px;
    margin-bottom: 40px;
    overflow: hidden;

    &-special {
      display: none;

      @include media-query($mobile) {
        display: block;
      }
    }

    &.view-details {
      margin-bottom: 40px;
      overflow: visible;

      @include media-query($mobile) {
        margin-bottom: 0;
      }

      &:before {
        display: block;
        content: '';
        background: rgba($dark, .5);
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        z-index: 40;
        pointer-events: none;
      }
      .speaker-block {
        z-index: 50;

        &:hover {
          img {
            transform: scale(1);
          }
        }
        img {
          transform: scale(1);
        }

        &:before {
          opacity: 0;
        }
        .speaker-block-data {
          opacity: 0;
        }
      }
      .speaker-block-details {
        display: block;
        z-index: 50;

        @include media-query($mobile) {
          display: flex;
          flex-direction: column;
        }
      }
    }

    &:nth-child(5n+1){
      .speaker-block-details {
        left: 0;
      }
    }
    &:nth-child(5n+2){
      .speaker-block-details {
        left: -224px;

        @include media-query-min($large) {
          left: -240px;
        }
        @include media-query-min($giant) {
          left: -280px;
        }
        @include media-query($small) {
          left: -100%
        }
        @include media-query($mobile) {
          left: 0;
        }
      }
    }
    &:nth-child(5n+3){
      .speaker-block-details {
        left: calc(-224px * 2);

        @include media-query-min($large) {
          left: calc(-240px * 2);
        }
        @include media-query-min($giant) {
          left: calc(-280px * 2);
        }
        @include media-query($small) {
          left: -200%;
        }
        @include media-query($mobile) {
          left: 0;
        }
      }
    }
    &:nth-child(5n+4){
      .speaker-block-details {
        left: calc(-224px * 3);

        @include media-query-min($large) {
          left: calc(-240px * 3);
        }
        @include media-query-min($giant) {
          left: calc(-280px * 3);
        }
        @include media-query($small) {
          left: -300%;
        }
         @include media-query($mobile) {
          left: 0;
        }
      }
    }
    &:nth-child(5n+5){
      .speaker-block-details {
        left: calc(-224px * 4);

        @include media-query-min($large) {
          left: calc(-240px * 4);
        }
        @include media-query-min($giant) {
          left: calc(-280px * 4);
        }
        @include media-query($small) {
          left: -400%;
        }
        @include media-query($mobile) {
          left: 0;
        }
      }
    }
    &:nth-child(5n+6){
      .speaker-block-details {
        left: calc(-224px * 5);

        @include media-query-min($large) {
          left: calc(-240px * 5);
        }
        @include media-query-min($giant) {
          left: calc(-280px * 5);
        }
        @include media-query($small) {
          left: -500%;
        }
        @include media-query($mobile) {
          left: 0;
        }
      }
    }
    @include media-query-min($large) {
      width: 240px;
      max-width: 240px;
    }
    @include media-query-min($giant) {
      width: 280px;
      max-width: 280px;
    }
    @include media-query($small) {
      width: 20%;
      max-width: 20%;
    }
    @include media-query($mobile) {
      width: 50%;
      max-width: 50%;
      margin-bottom: 0;
    }

    .speaker-block {
      display: block;
      height: 336px;
      min-height: 336px;
      overflow: hidden;
      position: relative;

      @include transition-ease;

      @include media-query-min($large) {
        height: 360px;
        min-height: 360px;
      }
      @include media-query-min($giant) {
        height: 421px;
        min-height: 421px;
      }
      @include media-query($small) {
        height: 100%;
        min-height: 100%;
      }
      @include media-query($mobile) {
        height: 280px;
        min-height: 280px;
      }

      &:before {
        display: block;
        content: '';
        width: 100%;
        height: 75%;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 20;
        background: -webkit-linear-gradient(transparent, rgba($dark, .8)) top;
        background: linear-gradient(transparent, rgba($dark, .8)) top;

        @include transition-ease;

        @include media-query($mobile) {
          height: 60%;
        }
      }

      &:hover {
        cursor: pointer;

        img {
          transform: scale(1.05);
        }

        .speaker-block-data h4 span {
          width: auto;
          height: auto;
          opacity: 1;
        }
      }

      &-data {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 20px;
        box-sizing: border-box;
        z-index: 30;

        @include transition-ease;

        @include media-query($small) {
          padding: 14px;
        }
        h5 {
          font-family: 'latobold', sans-serif;
          font-size: 14px;
          font-weight: normal;
          font-style: italic;
          line-height: 21px;
          letter-spacing: .45px;
          color: $gray;
        }

        h4 {
          font-family: 'latoblack', sans-serif;
          font-size: 11px;
          font-weight: normal;
          letter-spacing: 2px;
          line-height: 1;
          text-transform: uppercase;
          color: rgba($white, .5);
          border: 2px solid rgba($white, .2);
          padding: 6px 6px 6px 9px;
          display: inline-block;
          border-radius: 20px;
          overflow: hidden;

          &:hover {

            span {
              width: auto;
              height: auto;
              opacity: 1;
            }
          }
          span {
            display: inline-block;
            width: 0;
            height: 0;
            opacity: 0;
            overflow: hidden;
          }
          @include media-query($small) {
            font-size: 9px;
          }
          @include media-query($mobile) {
            font-size: 11px;
          }
        }
      }

      img {
        display: block;
        width: 100%;
        height: auto;

        @include transition-ease;
      }
    }

    .speaker-block-details {
      display: none;
      background: $blue-dark;
      width: 1120px;
      height: auto;
      position: relative;
      border-radius: 0 0 2px 2px;

      @include media-query-min($large) {
        width: 600%;
      }
      @include media-query($small) {
        width: 500%;
      }
      @include media-query($mobile) {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
      }
    }
  }
}

.modal-close, .update-close {
  display: block;
  width: 100%;
  padding: 24px;
  box-sizing: border-box;
  font-family: 'latobold', sans-serif;
  font-size: 12px;
  font-weight: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: right;
  color: rgba($white, .8);
  margin-left: auto;

  @include transition-ease;

  @include media-query($mobile) {
    text-align: center;
    margin-top: auto;
  }

  &:hover {
    color: $yellow;

    span:before, span:after {
      background: $yellow;
    }
  }

  span {
    display: inline-block;
    margin-left: 10px;
    margin-bottom: -2px;

    &:before, &:after {
      display: block;
      content: '';
      background: rgba($white, .8);

      @include transition-ease;
    }

    &:before {
      width: 13px;
      height: 1px;
      transform: rotate(45deg);
    }
    &:after {
      width: 1px;
      height: 13px;
      transform: rotate(45deg);
      margin-top: -7px;
      margin-left: 6px;
    }
  }
}

.speaker-block- {

  &-header {
    display: block;
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
  }

  &-content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;
    padding-bottom: 20px;
    box-sizing: border-box;
  }
}

/**
   LIST */

.partner- {
  display: block;
  width: 100%;

  &-list {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;

    margin-top: 38px;
    margin-bottom: 76px;
    width: 100%;

    &:last-of-type {
      margin-bottom: 0;
    }

    &-block {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-wrap: wrap;
      width: 100%;

      .partner--item:nth-child(4) {
        margin-right: 0;

        @include media-query($small) {
          margin-right: 5%;
        }
        @include media-query($mobile) {
          margin-right: 0;
        }
      }

      @include media-query($mobile) {
        justify-content: space-between;
      }
    }

    @include media-query($mobile) {
      margin-bottom: 24px;
    }

    h3 {
      display: block;
      width: 100%;
      margin-bottom: 20px;
    }

    &-single-group {
      justify-content: flex-start;

      @include media-query($mobile) {
        justify-content: space-between;
      }
    }

    &-inpost {
      justify-content: space-between;

      @include media-query($mobile) {
        padding: 0;
        margin-top: 20px;
      }

      .partner--item {
        width: calc(50% - 10px);
        margin-right: 0;

        @include media-query($mobile) {
          width: 100%;

          &:nth-child(4) {
            margin-right: 0;
          }
        }

        a {
          height: 100px;

          @include media-query($mobile) {
            height: 84px;
          }
        }

        &-large {

          a {
            height: 220px;

            @include media-query($mobile) {
              height: 190px;
            }
          }
        }
      }
    }
  }

  &-item {
    display: flex;
    align-items: center;
    justify-content: center;

    margin-bottom: 24px;
    margin-right: 24px;

    &:nth-child(4), &:nth-child(8), &:nth-child(12), &:nth-child(16) {
      margin-right: 0;
    }

    @include media-query($small) {
      width: 30%;
      margin-right: 5%;

      &:nth-child(3) {
        margin-right: 0;
      }
      &:nth-child(4) {
        margin-right: 5%;

        @include media-query($mobile) {
          margin-right: 0;
        }
      }
      &:nth-child(6) {
        margin-right: 0;
      }
    }

    @include media-query($mobile) {
      width: 48%;
      margin-bottom: 12px;
      margin-right: 0;
    }

    &-text {
      margin-right: 0;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 115px;
      padding: 0 40px;
      box-sizing: border-box;
      background: $white;
      border-radius: 4px;
      text-decoration: none;
      color: $blue-dark;

      @include transition-ease;

      @include media-query-min($large) {
        height: 140px;
      }

      @include media-query($mobile) {
        height: 90px;
        padding: 0 20px;
      }
      
      &:hover {
        background: rgba($white, .9);
        box-shadow: 0 4px 10px $blue-dark;
      }

      img {
        display: block;
        margin: 0 auto;
        max-height: 160px;
        height: auto;
        max-width: 100%;
        width: auto;
        width: initial;
      }

      span {
        font-family: 'latoblack', sans-serif;
        font-size: 23px;
        text-align: center;
        display: block;
      }
    }

    &-large {

      a {
        height: 210px;
      }
    }
  }
}


/**
  BUTTONS */

.button {
  display: inline-block;
  min-width: 120px;
  padding: 10px 38px;
  box-sizing: border-box;
  border-radius: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 10px;
  font-family: 'latobold', sans-serif;
  font-weight: normal;
  letter-spacing: 2px;
  box-shadow: 0 2px 4px rgba($dark, .1);

  @include transition-ease;

  @include media-query($mobile) {
    padding: 10px 20px;
  }

  @include media-query-min($large) {
    font-size: 12px;
  }
  @include media-query-min($giant) {
    font-size: 12px;
    padding: 12px 42px;
    border-radius: 24px;
  }

  &-secondary {
    background: none;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 12px;
    font-family: 'latobold', sans-serif;
    font-weight: normal;
    letter-spacing: 2px;

    margin-top: 20px;

    @include transition-ease;

    &:hover {
      color: $yellow;
    }

    &-gray {
      color: $gray;
      text-decoration: underline;
    }
  }
  &-third {
    background: $blue-darken;
    text-decoration: none;
    color: $yellow;
    padding: 20px;
    border-radius: 4px;

    &:hover {
      color: $white;
      background: $yellow;
    }
  }
}
.button-primary {
  background: $blue-dark;
  color: $white;

  &:hover {
    color: $yellow;
  }
}
.button-yellow {
  background: $yellow;
  color: $dark;

  &:hover {
    color: $white;
  }

  &-inactive {
    background: rgba($yellow, .5);
    color: $dark;

    &:hover {
      text-decoration: line-through;
    }
  }
}

.button-blue {
  background: $blue;
  color: $blue-dark;

  &:hover {
    color: $white;
  }
}
.button-red {
  background: $red;
  color: $dark;

  &:hover {
    color: $white;
  }
}
.button-green {
  background: $green;
  color: $dark;

