/**
  ARTICLES */

.post {
  position: relative;
  justify-content: flex-start;

  @include media-query($mobile) {
    width: 100%;
    flex-wrap: wrap;
  }

  &-meta {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 38px;

    &:last-of-type {
      margin-bottom: 0; 
    }

    @include media-query($mobile) {
      margin-bottom: 24px;
      position: relative;
      padding-left: 138px;
    }

    &-no-image {

      @include media-query($mobile) {
        padding-left: 0;
        flex-direction: row;

        .post-meta-item:first-of-type {
          margin-right: 24px;
        }
      }

    }

    &-row {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;

      .post-meta-item:last-of-type {
        margin-left: 48px;
      }

      @include media-query($mobile) {
        padding-left: 0;
      }
    }

    &-item {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      margin-bottom: 24px;

      @include transition-ease;

      @include media-query($mobile) {
        margin-bottom: 12px;
      }

      a.alt-title:hover {
        color: $yellow;
      }

      &-link {
        text-decoration: none;

        &:hover {

          .headline, .sub-headline {
            color: $yellow;
          }
        }
      }
      .button {
        width: 200px;
        margin-top: 24px;

        @include media-query-min($giant) {
          width: 260px;
        }
      }

      &-image {
        display: block;
        width: 100%;
        max-width: 159px;
        border-radius: 2px;
        box-sizing: border-box;

        @include media-query($mobile) {
          max-width: 114px;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }

    &-more-news {

      @include media-query($mobile) {
        display: none;
      }
    }

    &-coauthor {
      padding-left: 0;

      @include media-query($mobile) {
        flex-direction: row;
        padding-left: 0;

        .post-meta-item {
          width: 50%;
        }

        .alt-title-extra-small {
          font-size: 13px;
        }
      }

      .sub-headline {
        width: 100%;
      }
    }
  }

  &-image-full-width {
    display: block;
    margin: 0 auto;
  }

  &-content {

    ul, ol {
      margin-left: 20px;
      margin-bottom: 24px;

      li {
        line-height: 38px;
        color: $light-gray;

        @include media-query-min($giant) {
          font-size: 23px;
          line-height: 45px;
        }
      }

      a {
        font-family: 'latobold', sans-serif;
        font-weight: normal;
        color: $white;
        text-decoration: underline;

        @include transition-ease;

        &:hover {
          color: $yellow;
        }
      }
    }

    p strong {
      font-family: 'latobold', sans-serif;
      font-weight: normal;
      color: $white;
    }

    p:last-of-type {
      margin-bottom: 19px;
    }

    h2 {
      font-size: 29px;
      color: $yellow;
      margin-bottom: 24px;
    }
    h3 {
      font-size: 25px;
      color: $white;
      margin-bottom: 12px;

      @include media-query($mobile) {
        font-size: 21px;
      }
    }

    > h4 {
      font-family: 'latobold', sans-serif;
      font-size: 21px;
      font-weight: normal;
      line-height: 1.5;

      @include media-query-min($large){
        font-size: 27px;
      }
    }
  }

  &-header {
    margin-bottom: 36px;
  }

  &-pagination {
    display: flex;
    justify-content: flex-start;
    margin-top: 76px;

    @include media-query($mobile) {
      flex-wrap: wrap;
    }


    &-item {

      &.col-2-8 {

        @include media-query($mobile) {
          width: 100%;
        }
      }

      @include media-query($mobile) {

        &:nth-child(2) {
          margin-top: 24px;
        }
      }

      &:first-of-type {
        margin-left: 0;
      }

      h5 span:nth-child(2):before {
        display: inline-block;
        content: '&';
        margin: 0 6px 0 4px;
      }
    }

    a {
      font-family: 'latobold', sans-serif;
      text-decoration: none;

      @include transition-ease;

      &:hover {
        color: $yellow;
      }
    }
  }
}

.post-download-drop {
  margin-left: auto;
  display: block;
  position: relative;
  list-style: none;

  li:hover .inside-download-drop, li:target .inside-download-drop {
    display: block;
  }

  li {

    @include transition-ease;

    &:hover {
      color: $yellow;

      .download-tag .icon #arrow-down {
        fill: $yellow;
      }
    }
    .download-tag {
      display: inline-block;

      .icon {
        display: block;
        float: right;
        margin-left: 8px;
        margin-top: -2px;

        #arrow-down {
          fill: $white;

          @include transition-ease;
        }
      }
    }
  }


  .inside-download-drop {
    display: none;
    position: absolute;
    top: 30px;
    right: 0;
    list-style: none;

    background: #2b2f36;
    box-sizing: border-box;
    border-radius: 2px;
    z-index: 20;
    margin: 0;

    &:after {
      display: block;
      content: '';
      height: 140px;
      width: calc(100% + 20px);
      background: rgba($white, 0);
      position: absolute;
      top: -30px;
      left: -10px;
      z-index: -1;
    }

    li {
      display: block;
      box-sizing: border-box;
      border-bottom: 1px solid black;

      a {
        display: block;
        padding: 12px 20px;
        box-sizing: border-box;
        color: $white;
        font-family: 'latoblack', sans-serif;
        font-weight: normal;
        font-size: 11px;
        letter-spacing: 2px;
        text-decoration: none;
        text-transform: uppercase;

        @include transition-ease;

        &:hover {
          color: $yellow;
        }
      }
    }
  }
}

