      width: 432px;
    }
    @include media-query($small) {
      width: 30%;
    }
    @include media-query($mobile) {
      width: 100%;
      padding: 0 20px;
      box-sizing: border-box; 
    }

    p {
      font-size: 18px;
      color: rgba($white, .85);
      margin-bottom: 20px;

      @include media-query-min($large) {
        font-size: 23px;
      }
    }
  }
  .col-ml-1 {
    margin-left: 190px;

    @include media-query-min($large) {
      margin-left: 227px;
    }
    @include media-query($small) {
      margin-left: 0;
      margin-top: 20px;
    }
    @include media-query($mobile) {
      margin-left: 0;
    }
  }

  .col-duo-block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 48px;
    margin-bottom: 48px;
  }

  .workshop-program-group {
    
    @include media-query($small) {
      padding-left: 0;
      padding-right: 5%;
    }
    @include media-query($mobile) {
      padding: 0;
      order: 2;
      margin-top: 20px;
    }

    .workshop--block-item {
      width: 100%;
      background: none;

      @include media-query($mobile) {
        margin-bottom: 0;
      }

      &:hover {
        background: $blue-darken;

        img {
          border-radius: 2px 0 0 2px;
        }

        .workshop--block-title {
          color: $yellow;
        }
      }

      .workshop--block-content p {

        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;

        @include media-query-min($large) {
          font-size: 18px;
        }
      }

      img {
        width: 84px;
        max-width: 84px;
        border-radius: 2px;

        @include transition-ease;
      }

      .workshop--meta {
        border-bottom: none;
        padding-bottom: 0;
        margin-bottom: 5px;
      }
      .workshop--block-title {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        display: inline-block;
        width: 80%;

        @include media-query($mobile) {
          width: 100%;
        }

        @include transition-ease;
      }

      .workshop--block-status {
        background: $blue-dark;
        margin-bottom: 0;
        width: 110px;
        text-align: center;
        margin-right: 18px;

        &-active {
          background: $yellow;
        }

        @include media-query($mobile){
          margin-bottom: 8px;
        }
      }
    }
  }

  .headline {
    font-family: 'latoblack', sans-serif;
    font-size: 14px;
    font-weight: normal;
    letter-spacing: 2px;
    color: $yellow;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 33px;
    width: 100%;

    @include media-query-min($large) {
      font-size: 16px;
    }

    &:after {
      display: block;
      content: '';
      width: 40px;
      height: 1px;
      background: rgba(255, 255, 255, 0.5);
      position: absolute;
      bottom: -17px;
    }
  }

  p a {
    color: $yellow;
    text-decoration: none;

    @include transition-ease;

    &:hover {
      color: $white;
    }
  }

  .contact-logo {
    display: block;
    width: 240px;
    max-width: 240px;
    margin-bottom: 20px;
  }

  .full-width-image-details {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: center;
    -webkit-align-items: center;
  
    justify-content: center;
    -webkit-justify-content: center;
  
    flex-direction: column;
    -webkit-flex-direction: column;
    width: 100%;
    height: 288px;
    overflow: hidden;
    border-radius: 2px;

    @include media-query($small) {
      margin-top: 20px;
      width: 100%;
    }

    img {
      width: 100%;
    }
  }

  .clean-list {
    list-style: none;
    margin-bottom: 20px;

    li {
      margin-left: 0;
      margin-bottom: 0;
      font-size: 18px;
      color: rgba($white, .85);

      @include media-query-min($large) {
        font-size: 23px;
      }

      ul {
        margin-top: 4px;
        margin-left: 20px;
        list-style: disc;
      }

      a {
        display: block;
        margin-bottom: 14px;
        text-decoration: none;
        color: $white;

        @include transition-ease;

        &:hover {
          color: $yellow;

          .list-author {
            color: $yellow;
          }
        }

        .list-title {
          display: block;
        }
        .list-author {
          display: block;
          text-transform: uppercase;
          letter-spacing: 2px;
          font-family: 'latobold', sans-serif;
          font-weight: normal;
          font-size: 12px;
          color: rgba($white, .5);

          @include transition-ease;
        }
      }
    }
  }
}

/**
  ARCHIVE - SPEAKER */

.archive--speaker {

  &-header {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 76px;

    @include media-query($mobile){
      margin-bottom: 0;
    }
  }

  &-background {
    display: block;
    position: absolute;
    width: 100%;
    z-index: -5;

    @include media-query($mobile) {
      display: none;
    }

    &:after {
      display: block;
      content: '';
      width: 50%;
      height: 100%;
      background: linear-gradient(to right, rgba(3,1,0,1) 40%, rgba(3,1,0,0) 100%);
      position: absolute;
      top: 0;
    }

    &-image {
      display: block;
      width: 100%;
    }

    &-mask {
      display: block;
      content: '';
      width: 100%;
      height: 55%;
      position: absolute;
      bottom: 0;
      background: linear-gradient(to top, rgba(3,1,0,1) 50%, rgba(3,1,0,0) 100%);;
    }
  }

  &-introduction {

  }

  &-meta {
    display: block;
    margin-bottom: 19px;

    &-item {
      display: inline-block;
      border-radius: 4px;
      background: $yellow;
      color: $dark;
      padding: 8px 16px;
      box-sizing: border-box;

      font-family: 'latoblack', sans-serif;
      font-size: 14px;
      line-height: 1;
      letter-spacing: .45px;
      margin-right: 19px;
    }

  }

  &-info {
    display: block;
    margin-bottom: 38px;

    .title {
      color: $white;
    }

  }

  &-bio {

    .headline {
      font-size: 14px;
    }

    &-column {
      column-count: 2;
      column-gap: 24px;
    }

    &-column-single {
      column-count: 1;
      column-gap: 0;
    }
  }

  &-material {
    display: block;
    margin-top: 76px*2;
    margin-bottom: 76px;

    .title {
      color: $white;
    }
  }
  &-column-block {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }

  &-column-item {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: calc(50% - 12px);

    &:only-of-type {
      width: calc(75% - 12px);
    }

    &-solo {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 100%;

      .title {
        width: 100%;
      }
    }
  }

  &-list-item {
    display: block;
    margin-top: 19px;
    margin-bottom: 19px;

    .headline {
      font-size: 14px;
      color: $light-gray;
    }

    .sub-title {
      font-size: 29px;
      line-height: 45px;
      letter-spacing: -.45px;
      color: $yellow;
    }
    .speaker-role {
      display: block;
      margin-top: 19px;
      font-size: 18px;
    }
    &-keynote {
      color: $white;
    }
  }

  &-list-item-keynote {
    display: inline-block;
    position: relative;
    margin-left: 43px;

    &:before {
      display: inline-block;
      content: '';
      background: $light-gray;
      width: 19px;
      height: 1px;
      position: absolute;
      top: 50%;
      left: -31px;
      border-radius: 2px;
    }
  }

  &-pagination {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 1440px;
    margin: 0 auto;
    padding: 24px 0 0;
    box-sizing: border-box;

    @include media-query($mobile) {
      width: 100%;
      flex-wrap: wrap;
      padding: 24px 12px 0;
    }

    &-item {
      display: flex;
      flex-direction: column;

      .sub-headline {
        font-style: normal;
      }

      &-block {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin-top: 19px;

        @include media-query($mobile) {
          margin-top: 6px;
        }

        .speakers-block-item-image {
          width: 58px;
          height: 65px;
          -webkit-mask-image: url('../assets/icons/hexa-small.svg');
          mask-image: url('../assets/icons/hexa-small.svg');
          margin-right: 19px;

          @include media-query($mobile) {
            display: none;
          }
        }

        &-meta {
          display: flex;
          flex-direction: column;
        }
      }
    }

    .button {
      display: block;
      margin-left: auto;
    }
  }
}

.photo-copy {
  display: block;
  width: 100%;
  font-family: 'latoregular', sans-serif;
  font-weight: normal;
  font-size: 14px;
  font-style: italic;
  color: $gray;
  margin-top: 38px;
  text-align: right;
}

/**
  THANK YOU + Gallery */

.ty-box {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  align-items: center;
  -webkit-align-items: center;

  justify-content: center;
  -webkit-justify-content: center;

  flex-direction: column;
  -webkit-flex-direction: column;
  margin-bottom: 60px;

  img {
    display: block;
    max-width: 280px;

    @include media-query($mobile) {
      max-width: 140px;
    }

    &.end--logo {
      max-width: 120px;
      margin-top: 80px;
    }
  }

  h2 {
    display: block;
    margin-top: 20px;
    font-family: 'latoblack', sans-serif;
    font-size: 56px;

    @include media-query($mobile) {
      font-size: 38px;
    }
  }
  h3 {
    display: block;
    margin-top: 20px;
    font-family: 'latoregular', sans-serif;
    font-size: 26px;
  }
}

.ty-content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;

  &-item {
    width: calc(50% - 10px);
  }

  p {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    font-size: 18px;

    @include media-query($mobile) {
      width: 100%;
      padding: 0 20px;
      box-sizing: border-box;
    }

    a {
      color: $yellow;
      text-decoration: underline;

      &:hover {
        @include transition-ease;

        text-decoration: none;
        color: $white;
      }
    }

    b {
      font-family: 'latoblack', sans-serif;
      font-weight: 600;
    }

    i {
      color: $yellow;
    }
  }

  &-item-center {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    p {
      display: block;
      width: 100%;
      text-align: center;
    }
  }

  img {
    display: block;
    position: absolute;
    top: 0;
    right: 0;

    @include media-query($mobile) {
      position: relative;
      margin-bottom: 20px;
    }
  }
  .medium-image {
    display: block;
    width: 550px;

    @include media-query-min($large) {
      width: 659px;
    }

    @include media-query($mobile) {
      width: 100%;
    }
  }
}

.gallery {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  align-items: flex-start;
  -webkit-align-items: flex-start;

  justify-content: space-between;
  -webkit-justify-content: space-between;

  flex-direction: row;
  -webkit-flex-direction: row;
  flex-wrap: wrap;
  -webkit-flex-wrap: wrap;

  width: 100%;
  margin-top: 80px;

  @include media-query($mobile) {
    margin-top: 20px;
  }

  figure {
    display: block;
    margin-bottom: 20px;

    @include media-query($mobile) {
      margin-bottom: 10px;
    }

    img {
      vertical-align: top;
    }
  }

  .image-column {
    width: 550px;

    @include media-query-min($large) {
      width: 659px;
    }

    @include media-query($mobile) {
      width: 50%;

      .medium-image {
        width: 100%;
      }
    }

    img:last-of-type {
      margin-top: 20px;

      @include media-query($mobile) {
        margin-top: 10px;
      }
    }
  }

  .large-image img, .medium-image img, .small-image img, .xsmall-image img {
    width: 100%;
  }

  .large-image {
    width: 1120px;

    @include media-query-min($large) {
      width: 100%;
    }

    @include media-query($mobile) {
      width: 100%;
    }
  }
  .medium-image {
    width: 740px;

    @include media-query-min($large) {
      width: 886px;
    }

    @include media-query($mobile) {
      width: 100%;
    }
  }
  .small-image {
    width: 550px;

    @include media-query-min($giant) {
      width: 826px;
    }

    @include media-query($large) {
      width: 659px;
    }

    @include media-query($mobile) {
      width: 50%;
    }
  }
  .xsmall-image {
    width: 360px;

    @include media-query-min($large) {
      width: 432px;
    }

    @include media-query($mobile) {
      width: 33.3333%;
    }
  }
}

// CONTENT IMAGE

.col-full-width {
  display: flex;
  width: 100%;

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}


/**
  FOOTER */

.footer {
  display: block;
  width: 100%;
  max-width: 100%;
  margin-top: 76px;

  @include media-query($mobile) {
    margin-top: 38px;
  }

  &-inner {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;

  width: 100%;
  max-width: 1440px;
  padding: 0 0 40px;
  box-sizing: border-box;
  margin: 0 auto;

  @include media-query-min($giant) {
    max-width: 1680px;
  }

  @include media-query($medium) {
    max-width: 1200px;
  }

  @include media-query($small) {
    padding: 0 38px 38px; 
  }
  @include media-query($mobile) {
    padding: 0 12px 38px;
  }

    ul {
      display: block;
      width: 159px;
      max-width: 159px;
      list-style: none;
      margin-right: 24px;

      &:last-of-type {
        margin-right: 0;
      }

      @include media-query-min($giant) {
        width: 189px;
        max-width: 189px;
      }
      @include media-query($medium) {
        width: 129px;
        max-width: 129px;
      }
      @include media-query($small) {
        width: calc(25% - 20px);
        max-width: calc(25% - 20px);
        margin-bottom: 20px;
        margin-right: 0;
      }
      @include media-query($mobile) {
        width: 50%;
        max-width: 50%;
        margin: 0 0 20px;
      }

      li {

        span {
          display: inline-block;
          
          @include media-query($medium) {
            display: none;
          }
        }

        &:first-of-type {
          font-family: 'latoblack', sans-serif;
          font-weight: normal;
          font-size: 11px;
          text-transform: uppercase;
          color: $white;
          letter-spacing: 1px;

          @include media-query($medium){
            font-size: 12px;
          }

          @include media-query-min($giant) {
            font-size: 14px;
          }
        }

        a {
          display: block;
          font-family: 'latoregular', sans-serif;
          font-weight: normal;
          font-size: 11px;
          color: rgba($white, .8);
          letter-spacing: 1px;
          text-decoration: none;
          padding: 6px 0;
          box-sizing: border-box;

          @include transition-ease;

          @include media-query-min($large) {
            font-size: 13px;
          }
          @include media-query-min($giant) {
            font-size: 14px;
            line-height: 1.5;
          }

          &:hover {
            color: $yellow;
          }
        }
      }
    }
    .typemark {
      display: block;
      width: 100%;
      margin-top: 40px;
      font-family: 'latoregular', sans-serif;
      font-weight: normal;
      font-size: 11px;
      color: rgba($white, .8);
      letter-spacing: 1px;

      @include media-query-min($large) {
        font-size: 13px;
      }
    }
  }
}

.footer-agenda-block {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto 72px;
}